@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.header-container {
    background-color: #1B1B1B;
}

nav {
    position: fixed;
    width: 1200px;
    padding: 10px 10px 10px 10px;
    background-color: #1B1B1B;
    z-index: 1;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

nav .row {
    justify-content: space-between;
    align-items: center;
}

nav .brand-name h2{
    text-decoration: none;
    font-size: 22px;    
    font-weight: 600;
    color: #bbbbbb;
}

nav .navbar ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

nav .navbar ul li {
    display: inline-block;
    margin-left: 30px;
    padding-right: 50px;
}

nav .navbar ul li a{
    font-size: 15px;
    text-decoration: none;
    line-height: 17px;
    color: #bbbbbb;
    font-weight: 600;
}

.active {
    padding: 10px 15px;
    color: #333;
    color: rgba(255,255,255, 0.8);
    text-decoration: none;
    font-weight: bold;
    text-shadow: 0 1px 1px rgba(0,0,0, 0.3);
    transition: all 0.75s ease-in-out;
  }

li .active:hover, .active:focus {
    color: #fff;
    background: #fff;
    background: rgba(255,255,255, 0.2);
    border-radius: 4px;
    cursor: default;
  }

@media(max-width:450px) {
    nav {
        align-items: center;
    }
}
.home-container {
    min-height: 100vh;
    background-size: cover;
    padding-top: 100px;
    background-color: #EEEEEE;
    width: 100%;
}

.description {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
}
.intro {
    font-size: 30px;
}

.home-image {
    margin-top: 150px;
    width: 300px;
    box-shadow: 0 10px 20px rgba(0,0, 0, 0.05), 0 6px 6px rgba(0, 0, 0, 0.1 );
    border-radius: 5px;
    position: relative;
}
.skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
    padding-top: 100px;
    background-color: #EEEEEE;
}

.skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.card {
    background-color: white;
    box-shadow: 0 10px 20px rgba(0,0, 0, 0.05), 0 6px 6px rgba(0, 0, 0, 0.1 );
    border-radius: 5px;
    padding: 30px;
    margin: 15px;
    position: relative;
    overflow: hidden;
    width: 350px;
}

.card::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 5px;
    width: 100%;
}

.spring::after {
    background-color: hsl(103, 47%, 51%);
}

.react::after {
    background-color: hsl(187, 100%, 64%);
}

.postgres::after {
    background-color: hsl(207, 100%, 30%);
}

.docker::after {
    background-color: hsl(194, 100%, 36%);
}

.github::after {
    background-color: hsl(315, 100%, 6%);
}

.kubernetes::after {
    background-color: hsl(210, 100%, 36%);
}

.card h2 {
    margin: 0;
}
.card p {
    font-size: 14px;
    margin: 15px 0 30px;
}

.card img {
    display: block;
    margin-left: auto;
}
.blogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
    padding-top: 100px;
    background-color: #EEEEEE;
}
.about-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
    padding-top: 100px;
    background-color: #EEEEEE;
}
.footer-distributed {
    background-color: #292c2f;
    padding: 45px 50px;
  }
  
  .footer-distributed .footer-right {
    margin-top: 6px;
    max-width: 180px;
  }
  
  .footer-distributed .footer-right a {
    display: inline-block;
    width: 35px;
    height: 35px;
    background-color: #33383b;
    border-radius: 2px;
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;
    margin-left: 3px;
    transition:all .25s;
  }
  
  .footer-distributed .footer-right a:hover{transform:scale(1.1); -webkit-transform:scale(1.1);}
  
  @media (max-width: 600px) {
    .footer-distributed .footer-left, .footer-distributed .footer-right {
      text-align: center;
    }
    .footer-distributed .footer-right {
      float: none;
      margin: 0 auto 20px;
    }
    .footer-distributed .footer-left p.footer-links {
      line-height: 1.8;
    }
  }
* {
    box-sizing: border-box;
    margin: 0;
    
}

body {
    max-width: 1200px;
    margin: auto;
    font-family: 'Poppins', sans-serif;
}
